body {
    margin: 0;
}



#bar {
    width: 100vw;
    height: 50px;
    position: fixed;
    top: 0;
    z-index: 1;

    display: flex;
    justify-content: space-around;
    align-items: center;

    background-color: rgb(0, 0, 0, 0.1);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
            backdrop-filter: saturate(180%) blur(20px);

}


/* 每个导航栏按钮 */
.barLink {
    font-size: 22px;
    font-weight: 700;
    color: white;

    text-decoration: none;
}

@media screen and (max-width: 500px) {
    #bar {
        height:36px;
    }
    .barLink {
        font-size: 16px;
    }
}
.Basic-light {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
    transition: all 1s ease 0s;
}

.Basic-dark {
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
    transition: all 1s ease 0s;
}
.loading {
    width: 100vw;
    height: 100vh;

    display:flex;
    justify-content:center;
    align-items: center;
}
.footer {
    width: 100vw;
    height: 70px;

    background-color: rgb(288, 4, 81);

    display: flex;
    justify-content: center;
    align-items: center;
}
.footer > p {
    margin: 0px;
}
@media screen and (max-width: 600px) {
    
    .footer {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
}
.aboutMe {
    width: 100vw;
    min-height: 100vh;
    display:flex;
    justify-content: space-around;
    align-items: center;
    flex-direction:column;
    

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    color:wheat;
}

.about-title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.about-title > h1 {
    margin: 0;
}
#porfolioPage {
    min-height: 100vh;
    /* background-image: url(/image/GreenBGIEM.jpeg); */
    color: wheat;
}
.contactPage {
    width: 100vw;
    min-height: 100vh;
    display:flex;
    justify-content:center;
    align-items: center;

    background-position: center;
    background-repeat: no-repeat;
}
.error {
    width: 100vw;
    min-height: 100vh;
    display:flex;
    justify-content:center;
    align-items: center;

    background-position: center;
    background-repeat: no-repeat;
}
.theme-btn {
    position: fixed;
    top: 70px;
    right: 30px;

    width: 35px;
    height: 35px;

    background-size: 100%;
    background-position: center;

    z-index: 1;
}

